$bg_blue_color : #1eacc7;

@mixin info_buttons {
	display: block;
	margin: 0 auto;
	width: 112px;
	height: 24px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 400;
	text-transform: uppercase;
	border: none;
	cursor: text !important; }

* {
	font-family: 'Roboto', sans-serif; }

header {
	position: relative; }

nav {
	height: 51px;
	background-color: #212121; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style: none;

	.menu_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300; }

	.menu_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }

.subheader {
	position: absolute;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);

	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }

	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }

	.subheader_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }

	.subheader_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }

	.subheader_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $bg_blue_color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase; } }

.promo {
	min-height: 800px;
	background: url('../img/top_bg/top_bg.jpg') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;

	.promo_header {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }

	.promo_subheader {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }

	.promo_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }

	.promo_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $bg_blue_color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase; } }

.advantages {
	padding: 71px 0 65px 0; }

.label {
	position: relative;
	margin: 0 auto;
	width: 112px;
	height: 24px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 400;
	text-transform: uppercase;
	border: none;
	cursor: text !important;
	z-index: 2;
	&_white {
		background-color: #ffffff;
		color: #000; } }

.title {
	position: relative;
	margin: 32px 0 0;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	line-height: 48px;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		color: #ffffff; } }


.subtitle {
	position: relative;
	margin: 7px 0 0;
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center;
	z-index: 2;
	&_white {
		color: #ffffff; } }

.advantages {
	.advantages_wrapper {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;

		.advantages_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 26px;
			width: 113px;
			height: 113px;
			background-color: $bg_blue_color;
			border-radius: 100%; }

		.advantages_desc {
			width: 343px;

			.advantages_text {
				span {
					line-height: 21px;
					color: $bg_blue_color;
					font-size: 17px;
					font-weight: 400; }

				color: #202020;
				font-size: 15px;
				font-weight: 300;
				line-height: 21px; } } } }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/mobile_app.png') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/middle/1.jpg') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/middle/2.jpg') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/middle/3.jpg') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/middle/4.jpg') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/middle/5.jpg') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/middle/6.jpg') center (center / cover) no-repeat; }
		&_subtitle {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			border-radius: 100%;
			cursor: pointer; } } }

.capabilities {
	position: relative;
	min-height: 896px;
	padding: 57px 0 138px 0;
	background: url('../img/mobile_app.png') center (center / cover) no-repeat;
	&_car {
		position: relative;
		display: block;
		margin: 50px auto 0 auto;
		z-index: 2; }
	.block {
		position: absolute;
		left: 0;
		top: 0;
		height: 499px;
		background-color: #070716;
		width: 100%;
		z-index: 1; }
	&_desc {
		margin: 4px auto 0;
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		text-align: center; } }



